























import { Component, Vue } from "vue-property-decorator";
import Cognito from "../cognito";
import { mixins } from "vue-class-component";
import LoaderMixin from "../mixins/loader";

@Component
export default class Login extends mixins(LoaderMixin) {
  private valid: boolean = false;
  private emailRules: any;
  private passwrodRules: any;

  private email: string = "";
  private password: string = "";

  private cognito: Cognito;

  constructor() {
    super();

    this.cognito = new Cognito();

    this.emailRules = [
      // @ts-ignore
      v => !!v || "E-mail é obrigatório",
      // @ts-ignore
      v => /.+@.+/.test(v) || "Digite um email válido"
    ];

    this.passwrodRules = [
      // @ts-ignore
      v => !!v || "Campo obrigatório",
      // @ts-ignore
      v => (v && v.length >= 8) || "A senha deve conter pelo menos 8 caracteres"
    ];
  }

  private login() {
    // @ts-ignore
    if (this.$refs.form.validate()) {
      this.showLoader();

      this.cognito
        .authenticateUser(this.email, this.password)
        .then(session => {
          this.$store.commit("setUserSesion", session);
          alert("LOGADO");
          this.hideLoader();
        })
        .catch(err => {
          console.log(err);
          this.hideLoader();
        });
    }
  }

  private redirectSignUp() {
    this.$router.push("/cadastro");
  }
}
